.pagination-container {
  display: flex;
  gap: 0.5rem;
  martgin-top: 1rem;   
  justify-content: space-between;
  align-items: center;
} 

.pagination-container-hide {
  display: none;
}
  
.pagination-container button {
  margin-right: 0.2rem;
  margin-top: 0.5rem;
  border: none;
  width: 50px;
  height: 30px;
}

.pagination-container-no-employees-hide {
  display: none;
}

.pagination-container-no-employees {
  display: flex;
  gap: 0.5rem;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-top: 1rem;
  font-size: 1.5rem;
}

.pagination-container button:hover {   
  background-color: rgba(167, 164, 243, 0.4);
  cursor: pointer;
  border: 1px solid rgba(2, 2, 22, 0.4);
}

.number-of-pages {
  display: flex;
  align-items: center;
  padding-top: 0.5rem;
} 

.number-of-employees {
  display: flex;
  align-items: center;
  padding-top: 0.5rem;
}

.page-numbers {
  display: flex;
  align-items: center;
  padding-top: 0.5rem;
}

.left-group {
  display: flex;
  gap: 1rem;
}